var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("猎鹰")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        猎鹰（塞缪尔托马斯威尔逊）是一部虚构的 超级英雄，出现在由漫威漫画出版的美国漫画书中。该角色由作家兼编辑Stan Lee和艺术家Gene Colan创作，并在美国队长＃117（1969年9月）中介绍。作为超级英雄猎鹰，威尔逊使用机械翼飞行，并且对鸟类的心灵感应和移情控制有限。在史蒂夫罗杰斯退休后，威尔逊成为最新的美国队长和复仇者联盟的领袖。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        威尔逊已故的侄子是不可思议的绿巨人的有时候的伙伴吉姆威尔逊，这是首批公开的艾滋病毒阳性漫画人物之一。吉姆威尔逊的父亲吉迪恩威尔逊将继续加入伽玛军团。山姆还有一位名叫保罗和达琳威尔逊的父母，一位名叫萨拉卡斯珀的妹妹，一位名叫乔迪托比卡斯珀的侄子，和一位不知名的侄女。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        塞缪尔托马斯威尔逊，被称为猎鹰，是第一个在主流漫画书中的非洲裔美国超级英雄，也是第一个没有“黑色”这个词的黑人超级英雄作为他的超级英雄名字的一部分。这个角色最初出现在美国队长＃117（1969年9月）。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Colan在2008年回忆起， 由作家兼编辑Stan Lee和艺术家Gene Colan创作，在20世纪60年代后期当越南战争的新闻和民权抗议活动经常发生时，斯坦一直希望站在事物的最前沿，开始将这些头条新闻带入漫画。......我们在这方面采取的最大步骤之一来自美国队长。我喜欢吸引各种各样的人。我尽可能多地吸引不同类型的人进入我所说的场景，我喜欢画黑人。我总是发现他们的特征很有趣，他们的力量，精神和智慧都写在他们的脸上。我记得，我接近斯坦的想法是引进一位非裔美国人的英雄，他马上就接受了。我查看了几本非洲裔美国人的杂志，并将它们作为将猎鹰带入生活的灵感基础。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
